const events = [{
  code: 'ARRIVEE_SOS',
  textBefore: 'Les SOS dbarquent dans',
  date: new Date(2018, 1, 3, 23, 59, 0),
  inEvent: false
}, {
  code: 'DEBUT_PTIT_DEJ',
  textBefore: 'Le P\'tit Dej dbarque dans',
  date: new Date(2018, 1, 5, 8, 0, 0),
  inEvent: false
}, {
  code: 'FIN_PTIT_DEJ',
  textBefore: 'Le P\'tit Dej prend fin dans',
  date: new Date(2018, 1, 5, 17, 0, 0),
  inEvent: true
}, {
  code: 'DEBUT_KFET',
  textBefore: 'La KFET ouvre ses portes dans',
  date: new Date(2018, 1, 7, 18, 30, 0),
  inEvent: false
}, {
  code: 'FIN_KFET',
  textBefore: 'La KFET ferme ses porte dans',
  date: new Date(2018, 1, 7, 23, 30, 0),
  inEvent: true
}, {
  code: 'DEBUT_SOIREE',
  textBefore: 'La soire ouvre ses portes dans',
  date: new Date(2018, 1, 9, 20, 0, 0),
  inEvent: false
}, {
  code: 'FIN_SOIREE',
  textBefore: 'La soire ferme ses portes dans',
  date: new Date(2018, 1, 10, 4, 30, 0),
  inEvent: true
}, {
  code: 'DEBUT_ACTIVITE',
  textBefore: 'L\'activit libre arrive dans',
  date: new Date(2018, 1, 11, 12, 0, 0),
  inEvent: false
}, {
  code: 'FIN_ACTIVITE',
  textBefore: 'Activit libre en cours pendant',
  date: new Date(2018, 1, 11, 18, 0, 0),
  inEvent: true
}, {
  code: 'ENTREE_MANDAT',
  textBefore: 'On entre en mandat dans',
  date: new Date(2018, 7, 0, 0, 0, 0),
  inEvent: false
}, {
  code: 'INFINI',
  textBefore: 'Euh... l\'appli existe toujours ?',
  date: new Date(9999, 11, 0, 0, 0, 0),
  inEvent: false
}]

module.exports = _ => {
  const now = new Date()
  // On rcure la date la plue proche.
  let i = 0
  for (; events[i].date < now; i++);
  return events[i]
}



